.float-menu {
    position: fixed;
    z-index: 300;
    right: 2.4rem;
    top: 50%;
    transform: translate3d(0,50%,0);
    opacity: 0;
    transition: all 300ms ease;
    pointer-events: none;
    @include susy-breakpoint($s-320, $c8) {
        transform: translate3d(0,200%,0);
        right: 0;
    }
    @include susy-breakpoint($s-768, $c8) {
        transform: translate3d(0,200%,0);
        right: 0.8rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        right: 1.2rem;
    }
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__btn-beanfun {
        display: block;
        width: 15.8rem;
        height: 11.7rem;
        background: transparent url('../images/btn-beanfun!@3x.png') no-repeat 0 0;
        background-size: cover;
        font-size: 0;
        transition: all 300ms ease;
        margin: 0 auto;
        display: block;
        &:hover {
            transform: translate3d(0,5%,0);
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 15.8rem * 0.5;
            height: 11.7rem * 0.5;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 15.8rem * 0.6;
            height: 11.7rem * 0.6;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 15.8rem * 0.8;
            height: 11.7rem * 0.8;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 15.8rem * 0.8;
            height: 11.7rem * 0.8;
        }
    }
    &__btn-gotop {
        position: relative;
        width: 14.5rem;
        height: 14.5rem;
        background: transparent url('../images/bt-top@3x.png') no-repeat 0 0;
        background-size: cover;
        border: 0;
        padding: 0;
        font-size: 0;
        margin: 0 auto;
        display: block;
        transform: translate3d(0,-20%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 14.5rem * 0.5;
            height: 14.5rem * 0.5;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 14.5rem * 0.6;
            height: 14.5rem * 0.6;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 14.5rem * 0.8;
            height: 14.5rem * 0.8;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 14.5rem * 0.8;
            height: 14.5rem * 0.8;
        }
    }
}