%rsp-img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}

%inner {
    width: 120rem;
    margin: 0 auto;
    @include susy-breakpoint($s-320, $c8) {
        width: 100%;
    }
    @include susy-breakpoint($s-768, $c8) {
        width: 100%;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 94rem;
    }
}