.section-customer-service {
    padding-top: 6rem;
    @include susy-breakpoint($s-768, $c8) {
        padding-top: 4rem;
    }
    &__title {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        margin-bottom: 2.4rem;
        span {
            font-size: 5.2rem;
            color: #000;
            letter-spacing: 0.2rem;
            strong {
                color: var(--color-main-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 3.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 3.2rem;
            }
        }
        img {
            display: inline-block;
            width: 11rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 6rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 6rem;
            }
        }
    }
    &__item {
        h3 {
            font-size: 4rem;
            text-align: center;
            padding: 0.8rem;
            background-color: var(--color-main-500);
            color: #fff;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 2.4rem;
            }
        }
        ul {
            width: 54.8rem;
            margin: 0 auto;
            padding: 2.4rem 0;
            @include susy-breakpoint($s-320, $c8) {
                width: 100%;
                padding-top: 1.6rem;
                padding-bottom: 1.6rem;
            }
            li {
                font-size: 2rem;
                color: #707070;
                font-weight: bolder;
                text-align: center;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.6rem;
                }
                @include susy-breakpoint($s-768, $c8) {
                    font-size: 1.6rem;
                }
                &:not(:last-child) {
                    margin-bottom: 2.4rem;
                    @include susy-breakpoint($s-320, $c8) {
                        margin-bottom: 1.2rem;
                    }
                    @include susy-breakpoint($s-768, $c8) {
                        margin-bottom:2rem;
                    }
                }
                a {
                    color: var(--color-main-500);
                }
            }
        }
    }
}