.popup-ui {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    pointer-events: none;
    z-index: 300;
    transition: all 300ms ease;
    &.active {
        pointer-events: auto;
        opacity: 1;
    }
    &__overlay {
        background-color: rgba(0,0,0,0.6);
        display: flex;
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
        &.scroll {
            overflow-y: scroll;
            align-items: flex-start;
        }
    }
    &__inner {
        width: 120rem;
        position: relative;
        @include susy-breakpoint($s-320, $c8) {
            box-sizing: border-box;
            width: 100%;
            padding: 1.6rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            box-sizing: border-box;
            width: 48rem;
            padding: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 94rem;
        }
    }
    &__banner {
        width: 100%;
        height: 67rem;
        background-color: rgba(38,208,124,0.71);
        padding: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem;
            height: 56rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding: 1.2rem;
            height: 56rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            height: 56rem;
        }
    }
    &__banner-inner {
        width: 100%;
        height: 100%;
        position: relative;
        background-color: #fff;
        padding: 8rem 7.2rem;
        box-sizing: border-box;
        @include susy-breakpoint($s-320, $c8) {
            padding: 2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 5.6rem 4rem;
        }
    }
    &__gamania-title {
        display: block;
        width: 49.4rem;
        margin-bottom: 2.4rem;
        img {
            @extend %rsp-img;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 20rem;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 20rem;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 1.2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 40rem;
        }
    }
    &__title {
        font-size: 2.1rem;
        color: #374364;
        margin-bottom: 6rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
            line-height: 1.6;
            margin-bottom: 2.4rem;
            text-align: center;
            span {
                display: block;
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 1.6rem;
            line-height: 1.6;
            margin-bottom: 2.4rem;
            text-align: center;
            span {
                display: block;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.7rem;
            margin-bottom: 4rem;
        }
    }
    &__text {
        font-size: 2rem;
        color: #5c6886;
        line-height: 1.6;
        font-weight: bolder;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
            text-align: center;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 1.6rem;
            text-align: center;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.7rem;
        }
    }
    &__tree {
        width: 50.2rem;
        height: 57.9rem;
        position: absolute;
        bottom: 0;
        right: 7.2rem;
        background: transparent url('../images/popup-tree@3x.png') no-repeat 0 0;
        background-size: cover;
        @include susy-breakpoint($s-320, $c8) {
            width: 20rem;
            height: 23rem;
            right: inherit;
            left: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 20rem;
            height: 23rem;
            right: inherit;
            left: 2.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 50.2rem * 0.7;
            height: 57.9rem * 0.7;
            right: 6.6rem;
        }
    }
    &__btn-close {
        background: transparent url('../images/btn-close@3x.png') no-repeat 0 0;
        background-size: cover;
        width: 5rem;
        height: 5rem;
        position: absolute;
        z-index: 50;
        padding: 0;
        right: 4.2rem;
        top: 4.2rem;
        border: 0;
        font-size: 0;
        @include susy-breakpoint($s-320, $c8) {
            width: 3.6rem;
            height: 3.6rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 3.6rem;
            height: 3.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 4.4rem;
            height: 4.4rem;
        }
    }
    &__btn-more {
        border-radius: 2rem;
        display: block;
        font-size: 3rem;
        padding: 2.2rem 7.2rem 2.2rem 2rem;
        position: absolute;
        bottom: 4.8rem;
        right: 4.8rem;
        background-color: #26d07c;
        color: #fff;
        font-weight: bolder;
        &::before {
            content: "";
            left: 0.8rem;
            top: 50%;
            transition: all 300ms ease;
            transform: translate3d(0,-170%,0);
            position: absolute;
            width: 5.4rem;
            height: 4.9rem;
            background: transparent url('../images/popup-squirrel@3x.png') no-repeat 0 0;
            background-size: cover;
            pointer-events: none;
        }
        &::after {
            content: "";
            right: 1.8rem;
            top: 50%;
            transition: all 300ms ease;
            transform: translate3d(0,-50%,0);
            position: absolute;
            width: 5rem;
            height: 2.6rem;
            background: transparent url('../images/icon-arrow-2@3x.png') no-repeat 0 0;
            background-size: cover;
            pointer-events: none;
        }
        &:hover {
            &::after {
                transform: translate3d(10%,-50%,0);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            padding: 1.2rem 4.8rem 1.2rem 1.2rem;
            font-size: 1.8rem;
            border-radius: 1.2rem;
            bottom: 1.4rem;
            right: 1.4rem;
            &::before {
                width: 2rem;
                height: 1.6rem;
                transform: translate3d(25%,-230%,0);
            }
            &::after {
                width: 3rem;
                height: 1.6rem;
                right: 1rem;
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            padding: 1.2rem 4.8rem 1.2rem 1.2rem;
            font-size: 1.8rem;
            border-radius: 1.2rem;
            bottom: 1.4rem;
            right: 1.4rem;
            &::before {
                width: 2rem;
                height: 1.6rem;
                transform: translate3d(25%,-230%,0);
            }
            &::after {
                width: 3rem;
                height: 1.6rem;
                right: 1rem;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 1.6rem 5.6rem 1.6rem 1.6rem;
            font-size: 2rem;
            border-radius: 1.6rem;
            bottom: 2.4rem;
            right: 2.4rem;
            &::before {
                width: 3rem;
                height: 2.4rem;
                transform: translate3d(2%,-208%,0);
            }
            &::after {
                width: 3rem;
                height: 1.6rem;
            }
        }
    }
}