.section-exchange {
    position: relative;
    padding-bottom: 1.6rem;
    &::before {
        width: 100%;
        height: 32.2rem;
        position: absolute;
        bottom: 0;
        left: 0;
        background: transparent url('../images/section-exchange-bg.png') no-repeat center top;
        background-size: auto 100%;
        content: "";
        z-index: 0;
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
        @include susy-breakpoint($s-768, $c8) {
            display: none;
        }
    }
    &__inner {
        @extend %inner;
        position: relative;
        z-index: 10;
    }
    &__title {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        margin-bottom: 2.4rem;
        span {
            font-size: 5.2rem;
            color: #000;
            letter-spacing: 0.2rem;
            strong {
                color: var(--color-main-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 4rem;
                strong {
                    display: block;
                }
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 4rem;
                strong {
                    display: block;
                }
            }
        }
        img {
            display: inline-block;
            width: 13.3rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 9.6rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 9.6rem;
            }
        }
    }
    &__content {
        background-color: var(--color-main-500);
        box-shadow: 0px 7px 24px 0px rgba(0, 0, 0, 0.3);
        border-radius: 8.6rem;
        padding: 2rem;
        display: flex;
        margin-left: 2rem;
        margin-right: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-left: 0;
            margin-right: 0;
            display: block;
            border-radius: 5rem;
            padding: 1rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            display: block;
            border-radius: 5rem;
            padding: 1rem;
            margin-left: 3rem;
            margin-right: 3rem;
        }
    }
    &__tabs {
        width: 19.8rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            display: flex;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 100%;
            display: flex;
        }
    }
    &__btn-tab {
        font-weight: bolder;
        width: 100%;
        height: 32.5rem;
        display: flex;
        background-color: var(--color-main-500);
        border-top-left-radius: 6.6rem;
        border-bottom-left-radius: 6.6rem;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        line-height: 1.4;
        font-size: 3.2rem;
        color: #fff;
        transition: all 300ms ease;
        cursor: pointer;
        small {
            display: block;
            font-size: 2.2rem;
        }
        &:hover {
            background-color: #fff;
            color: var(--color-main-500);
        }
        &.active {
            background-color: #fff;
            color: var(--color-main-500);
            pointer-events: none;
        }
        @include susy-breakpoint($s-320, $c8) {
            height: 11.4rem;
            font-size: 2.4rem;
            text-align: center;
            border-top-left-radius: 4rem;
            border-top-right-radius: 4rem;
            border-bottom-left-radius: 0;
            small {
                font-size: 1.6rem;
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            height: 11.4rem;
            font-size: 2.4rem;
            text-align: center;
            border-top-left-radius: 4rem;
            border-top-right-radius: 4rem;
            border-bottom-left-radius: 0;
            small {
                font-size: 1.6rem;
            }
        }
    }
    &__tab-wrap {
        background-color: #fff;
        flex: 1;
        border-top-right-radius: 6.6rem;
        border-bottom-right-radius: 6.6rem;
        position: relative;
        overflow: hidden;
        @include susy-breakpoint($s-320, $c8) {
            height: 81.8rem;
            width: 100%;
            border-top-right-radius: 0;
            border-bottom-right-radius: 4rem;
            border-bottom-left-radius: 4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            height: 99rem;
            width: 100%;
            border-top-right-radius: 0;
            border-bottom-right-radius: 4rem;
            border-bottom-left-radius: 4rem;
        }
    }
    &__tab-content {
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        pointer-events: none;
        transition: all 300ms ease;
        &.active {
            opacity: 1;
            pointer-events: auto;
        }
        .swiper-button-prev {
            width: 4.9rem;
            height: 10.6rem;
            background: transparent url('../images/btn-prev@3x.png');
            background-size: cover;
            left: 2rem;
            opacity: 0.5;
            transition: opacity 300ms ease;
            &::after {
                display: none;
            }
            &:hover {
                opacity: 1;
            }
            @include susy-breakpoint($s-320, $c8) {
                width: 3.5rem;
                height: 7.6rem;
                left: 1rem;
                transform: translate3d(0, 70%, 0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 3.5rem;
                height: 7.6rem;
                left: 2rem;
                transform: translate3d(0, 40%, 0);
            }
        }
        .swiper-button-next {
            width: 4.9rem;
            height: 10.6rem;
            background: transparent url('../images/btn-next@3x.png');
            background-size: cover;
            right: 2rem;
            opacity: 0.5;
            transition: opacity 300ms ease;
            &::after {
                display: none;
            }
            &:hover {
                opacity: 1;
            }
            @include susy-breakpoint($s-320, $c8) {
                width: 3.5rem;
                height: 7.6rem;
                right: 1rem;
                transform: translate3d(0, 70%, 0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 3.5rem;
                height: 7.6rem;
                right: 2rem;
                transform: translate3d(0, 40%, 0);
            }
        }
        .swiper-slide {
            padding-top: 3rem;
            position: relative;
            @include susy-breakpoint($s-320, $c8) {
                padding-top: 16rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                padding-top: 10rem;
            }
        }
        .swiper-pagination {
            bottom: inherit;
            top: 2rem;
            display: flex;
            align-items: center;
            justify-content: center;
            @include susy-breakpoint($s-320, $c8) {
                top: 2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                top: 3rem;
            }
        }
        .swiper-pagination-bullet {
            width: 2.2rem;
            height: 2.2rem;
            background-color: #d1d1d1;
            opacity: 1;
            border-radius: 1rem;
            margin-left: 1rem !important;
            margin-right: 1rem !important;
            @include susy-breakpoint($s-320, $c8) {
                width: 1.6rem;
                height: 1.6rem;
                border-radius: 50%;
                margin-left: 0.4rem !important;
                margin-right: 0.4rem !important;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 1.6rem;
                height: 1.6rem;
                border-radius: 50%;
                margin-left: 0.8rem !important;
                margin-right: 0.8rem !important;
            }
        }
        .swiper-pagination-bullet-active {
            width: 3.2rem;
            height: 3.2rem;
            background-color: var(--color-main-500);
            border-radius: 1.3rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 2rem;
                height: 2rem;
                border-radius: 50%;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2rem;
                height: 2rem;
                border-radius: 50%;
            }
        }
    }
    &__slider-img {
        margin-left: 5rem;
        height: 100%;
        background-color: #fff;
        @include susy-breakpoint($s-320, $c8) {
            margin: 0 auto;
        }
        @include susy-breakpoint($s-768, $c8) {
            margin: 0 auto;
        }
        img {
            height: 94.8rem;
            @include susy-breakpoint($s-320, $c8) {
                height: auto;
                max-width: 90%;
                display: block;
                margin: 0 auto;
            }
            @include susy-breakpoint($s-768, $c8) {
                height: auto;
                max-width: 48rem;
                display: block;
                margin: 0 auto;
            }
        }
    }
    &__slider-content {
        position: absolute;
        width: 27.6rem;
        top: 29rem;
        left: 50%;
        transform: translate3d(40%,0,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            top: 0;
            left: 0;
            transform: translate3d(0,6rem,0);
            display: flex;
            align-items: flex-start;
            padding: 0 4rem;
            box-sizing: border-box;
            gap: 1.6rem
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 48rem;
            top: 0;
            left: 50%;
            transform: translate3d(-50%,7rem,0);
            display: flex;
            align-items: flex-start;
            padding: 0 4rem;
            box-sizing: border-box;
            gap: 1.6rem;
        }
        p {
            font-weight: bolder;
            font-size: 2rem;
            line-height: 1.6;
            color: #000;
            span {
                color: #fd8103;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.6rem;
            }
        }
    }
    &__br-pc {
        display: none;
    }
    &__num {
        span {
            width: 5.6rem;
            height: 5.6rem;
            background-color: var(--color-main-500);
            border-radius: 50%;
            font-size: 3.6rem;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: bolder;
            color: #fff;
            @include susy-breakpoint($s-320, $c8) {
                width: 4rem;
                height: 4rem;
                font-size: 2.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 4rem;
                height: 4rem;
                font-size: 2.4rem;
            }
        }
    }
}