.section-kv {
    height: 85.9rem;
    background: #fff url('../images/kv.jpg') no-repeat center bottom;
    background-size: auto 100%;
    @include susy-breakpoint($s-320, $c8) {
        background-image: url('../images/kv-m.jpg');
        height: 120vw;
        background-size: 100% auto;
    }
    @include susy-breakpoint($s-768, $c8) {
        height: 54rem;
        background-size: auto 90%;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 64rem;
        background-size: auto 100%;
    }
    &__inner {
        padding-top: 18rem;
        box-sizing: border-box;
        width: 120rem;
        height: 100%;
        margin: 0 auto;
        @include susy-breakpoint($s-320, $c8) {
            width: auto;
            padding-top: 7.4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 100%;
            padding-top: 10rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 90rem;
            padding-top: 14rem;
        }
        h1 {
            padding-left: 5.6rem;
            line-height: 1.4;
            font-size: 5.7rem;
            color: #000;
            span {
                color: var(--color-main-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2.2rem;
                padding-left: 0;
                width: 100%;
                text-align: center;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 3.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 4rem;
                padding-left: 4rem;
            }
        }
    }
}