.site-footer {
    background-color: #333;
    &__inner {
        @extend %inner;
        @include susy-breakpoint($s-768, $c8) {
            justify-content: center;
        }
    }
    &__gamania-logo {
        width: 27.2rem;
        height: 10.5rem;
        margin-right: 13.2rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 12.6rem;
            height: 4.9rem;
            margin-left: auto;
            margin-right: auto;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 27.2rem * 0.8;
            height: 10.5rem * 0.8;
            margin-right: 4rem;
        }
    }
    &__content {
        display: flex;
        padding-top: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            flex-direction: column;
            padding-top: 3rem;
            gap: 3rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            justify-content: center;
            padding-bottom: 2rem;
        }
    }
    &__info {
        padding: 0.6rem 0 0 5.6rem;
        border-left: solid 0.1rem #fff;
        @include susy-breakpoint($s-320, $c8) {
            border-left: 0;
            padding: 0 2rem 0 2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding: 0 2rem 0 4rem;
        }
        li {
            font-size: 2rem;
            color: #fff;
            margin-bottom: 2rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                text-align: center;
                margin-bottom: 1.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.6rem;
                margin-bottom: 1.6rem;
            }
            a {
                color: var(--color-main-500);
            }
        }
    }
    &__copyright {
        padding: 1.6rem 0;
        font-size: 1.5rem;
        color: #fff;
        line-height: 1.6;
        text-align: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.2rem;
            padding-top: 2rem;
            padding-bottom: 2rem;
        }
    }
}