.section-qa {
    $self: &;
    padding-top: 4rem;
    padding-bottom: 4rem;
    &__inner {
        width: 100rem;
        margin: 0 auto;
        @include susy-breakpoint($s-320, $c8) {
            width: auto;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 100%;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 94rem;
        }
    }
    &__title {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        padding-bottom: 2rem;
        margin-bottom: 2.4rem;
        border-bottom: 0.1rem solid var(--color-main-500);
        span {
            font-size: 5.2rem;
            color: #000;
            letter-spacing: 0.2rem;
            strong {
                color: var(--color-main-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 3.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 3.2rem;
            }
        }
        img {
            display: inline-block;
            width: 16.2rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 8.7rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 8.7rem;
            }
        }
    }
    &__content {
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }
    &__item {
        &:not(:last-child) {
            margin-bottom: 4rem;
        }
        h3 {
            font-size: 2.6rem;
            color: #000;
            font-weight: bolder;
            border-bottom: 0.1rem solid #d7d6d6;
            padding-bottom: 1.2rem;
            margin-bottom: 2rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 2rem;
            }
        }
    }
    &__qa-list {
        li {
            cursor: pointer;
            border-bottom: 0.1rem solid #d7d6d6;
            margin-bottom: 2rem;
            padding-bottom: 2rem;
            &.active {
                #{$self}__question {
                    &::before {
                        transform: translate3d(30%, 28%, 0);
                        @include susy-breakpoint($s-320, $c8) {
                            transform: translate3d(30%, 40%, 0);
                        }
                        @include susy-breakpoint($s-768, $c8) {
                            transform: translate3d(30%, 50%, 0);
                        }
                    }
                }
            }
        }
    }
    &__question {
        font-size: 2.6rem;
        color: var(--color-main-500);
        font-weight: bolder;
        padding-left: 4.6rem;
        position: relative;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
            padding-left: 3rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 2rem;
            padding-left: 3rem;
        }
        &::before {
            width: 2.8rem;
            height: 2.8rem;
            position: absolute;
            left: 0;
            top: 0;
            background: transparent url('../images/icon-arrow@3x.png') no-repeat center top;
            background-size: cover;
            content: "";
            transform: translate3d(30%, 28%, 0) rotate(-90deg);
            transition: all 300ms ease;
            @include susy-breakpoint($s-320, $c8) {
                width: 1.4rem;
                height: 1.4rem;
                transform: translate3d(30%, 40%, 0) rotate(-90deg);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 1.6rem;
                height: 1.6rem;
                transform: translate3d(30%, 50%, 0) rotate(-90deg);
            }
        }
    }
    &__answer {
        display: none;
        padding: 1.6rem 0 0 4.8rem;
        font-size: 2rem;
        color: #707070;
        font-weight: bolder;
        line-height: 1.6;
        a {
            color: var(--color-main-500);
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
            padding-left: 3rem;
            padding-top: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 1.6rem;
            padding-left: 3rem;
        }
    }
}