.section-discount {
    padding-top: 14rem;
    padding-bottom: 14rem;
    @include susy-breakpoint($s-320, $c8) {
        padding-top: 6rem;
        padding-bottom: 6rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        padding-top: 8rem;
        padding-bottom: 8rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        padding-top: 10rem;
        padding-bottom: 10rem;
    }
    &__inner {
        @extend %inner;
        display: flex;
        gap: 4.8rem;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            flex-direction: column;
            padding: 0 4.8rem;
        }
    }
    &__gift-box {
        flex-basis: 42.8rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0 auto;
        }
        @include susy-breakpoint($s-320, $c8) {
            order: 2;
            flex-basis: 100%;
            img {
                width: 20rem;
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            flex-basis: 30rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            flex-basis: 36rem;
        }
    }
    &__content {
        flex-basis: 57.8rem;
        @include susy-breakpoint($s-320, $c8) {
            order: 1;
            flex-basis: 100%;
        }
        @include susy-breakpoint($s-768, $c8) {
            flex-basis: 36rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            flex-basis: 50rem;
        }
        h2 {
            color: var(--color-orange-500);
            font-size: 15.5rem;
            text-align: center;
            margin-bottom: 2rem;
            small {
                display: inline-block;
                font-size: 10.8rem;
                transform: translate3d(0,-10%,0);
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 9.2rem;
                small {
                    font-size: 5.8rem;
                }
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 9.2rem;
                small {
                    font-size: 5.8rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 14rem;
                small {
                    font-size: 10rem;
                }
            }
        }
        h3 {
            background-color: var(--color-orange-500);
            padding: 1.2rem 0;
            text-align: center;
            font-size: 3.7rem;
            color: #fff;
            margin-bottom: 3.2rem;
            strong {
                font-size: 4.7rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 2.4rem;
                font-size: 2.4rem;
                padding-top: 0.8rem;
                padding-bottom: 0.8rem;
                strong {
                    font-size: 2.7rem;
                }
            }
            @include susy-breakpoint($s-768, $c8) {
                margin-bottom: 2.4rem;
                font-size: 2.4rem;
                padding-top: 0.8rem;
                padding-bottom: 0.8rem;
                strong {
                    font-size: 2.7rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 3.2rem;
                strong {
                    font-size: 4rem;
                }
            }
        }
        h4 {
            font-size: 2.6rem;
            color: #000;
            margin-bottom: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.8rem;
                margin-bottom: 0.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 2rem;
                margin-bottom: 0.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2.4rem;
            }
        }
        p {
            font-size: 2rem;
            color: #707070;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.8rem;
            }
        }
    }
}