.site-header {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    background-color: #fff;
    height: 11.5rem;
    box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.19);
    z-index: 200;
    @include susy-breakpoint($s-320, $c8) {
        height: 4.4rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        height: 4.4rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 9rem;
    }
    &__inner {
        @extend %inner;
        display: flex;
        align-items: center;
        height: 100%;
        justify-content: space-between;
        @include susy-breakpoint($s-320, $c8) {
            position: relative;
            box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.19);
            background-color: #fff;
        }
        @include susy-breakpoint($s-768, $c8) {
            position: relative;
            box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.19);
            background-color: #fff;
        }
    }
    &__beanfun {
        width: 11rem;
        height: 2.2rem;
        background: transparent url('../images/beanfun-logo.svg') no-repeat 0 0;
        background-size: cover;
        @include susy-breakpoint($s-320, $c8) {
            width: 8.5rem;
            height: 1.8rem;
            margin-left: 1.6rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: 8.5rem;
            height: 1.8rem;
            margin-left: 1.6rem;
        }
        a {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
    &__nav {
        @include susy-breakpoint($s-320, $c8) {
            position: absolute;
            left: 0;
            width: 100%;
            top: 4.4rem;
            background-color: #fff;
            box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.19);
            z-index: 0;
            transform: translate3d(0,-120%,0);
            transition: all 300ms ease;
            z-index: -1;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            position: absolute;
            left: 0;
            width: 100%;
            top: 4.4rem;
            background-color: #fff;
            box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.19);
            z-index: 0;
            transform: translate3d(0,-120%,0);
            transition: all 300ms ease;
            z-index: -1;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
    }
    &__nav-list {
        display: flex;
        height: 11.5rem;
        @include susy-breakpoint($s-320, $c8) {
            height: auto;
            flex-direction: column;
        }
        @include susy-breakpoint($s-768, $c8) {
            height: auto;
            flex-direction: column;
        }
        @include susy-breakpoint($s-960, $c12) {
            height: 9rem;
        }
        & > li {
            position: relative;
            @include susy-breakpoint($s-320, $c8) {
                padding: 0 1.6rem;
                &:last-child {
                    a {
                        border-bottom: 0;
                    }
                }
            }
            @include susy-breakpoint($s-768, $c8) {
                padding: 0 1.6rem;
                &:last-child {
                    a {
                        border-bottom: 0;
                    }
                }
            }
            & > a {
                color: #000;
                position: relative;
                z-index: 10;
                background-color: #fff;
                font-size: 1.6rem;
                letter-spacing: 0.1rem;
                font-weight: bolder;
                display: flex;
                align-items: center;
                height: 100%;
                padding: 0 2rem;
                transition: all 300ms ease;
                &:hover {
                    background-color: var(--color-main-500);
                    color: #fff;
                }
                &.active {
                    background-color: var(--color-main-500);
                    color: #fff;
                }
                @include susy-breakpoint($s-320, $c8) {
                    padding: 1.6rem 0;
                    border-bottom: 0.1rem solid #bdbcbc;
                    &:hover {
                        background-color: #fff;
                        color: #000;
                    }
                    &.active {
                        background-color: #fff;
                        color: #000;
                    }
                }
                @include susy-breakpoint($s-768, $c8) {
                    padding: 1.6rem 0;
                    border-bottom: 0.1rem solid #bdbcbc;
                    &:hover {
                        background-color: #fff;
                        color: #000;
                    }
                    &.active {
                        background-color: #fff;
                        color: #000;
                    }
                }
                @include susy-breakpoint($s-960, $c12) {
                    padding-left: 1rem;
                    padding-right: 1rem;
                }
            }
        }
    }
    &__btn-sub-link {
        position: relative;
        &::after {
            transition: all 100ms ease;
            display: inline-block;
            margin-left: .255em;
            vertical-align: .255em;
            content: "";
            border-top: .3em solid;
            border-right: .3em solid transparent;
            border-bottom: 0;
            border-left: .3em solid transparent;
        }
        &:hover {
            &::after {
                border-top-color: #fff;
            }
        }
        &.active {
            &::after {
                transform: rotate(180deg);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            &.active {
                border-bottom: solid 0.1rem var(--color-main-500);
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            &.active {
                border-bottom: solid 0.1rem var(--color-main-500);
            }
        }
    }
    &__sub-nav-list {
        background-color: #fff;
        width: 100%;
        box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.19);
        position: absolute;
        z-index: 5;
        display: none;
        @include susy-breakpoint($s-320, $c8) {
            position: relative;
            box-shadow: none;
            border: solid 0.1rem var(--color-main-500);
            border-radius: 0.8rem;
            margin-top: 0.4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            position: relative;
            box-shadow: none;
            border: solid 0.1rem var(--color-main-500);
            border-radius: 0.8rem;
            margin-top: 0.4rem;
        }
        li {
            @include susy-breakpoint($s-320, $c8) {
                padding: 0 0.8rem;
                &:last-child {
                    a {
                        border-bottom: 0;
                    }
                }
            }
            @include susy-breakpoint($s-768, $c8) {
                padding: 0 0.8rem;
                &:last-child {
                    a {
                        border-bottom: 0;
                    }
                }
            }
        }
        a {
            font-size: 1.6rem;
            color: #000;
            width: 100%;
            display: block;
            padding: 2rem 0;
            text-align: center;
            transition: all 300ms ease;
            letter-spacing: 0.1rem;
            font-weight: bolder;
            &:hover {
                background-color: #f6f6f6;
            }
            &.active {
                background-color: #f6f6f6;
            }
            @include susy-breakpoint($s-320, $c8) {
                border-bottom: solid 0.1rem var(--color-main-500);
                text-align: left;
                padding: 1.6rem 0.8rem;
                &:hover {
                    background-color: transparent;
                }
                &.active {
                    background-color: transparent;
                }
            }
            @include susy-breakpoint($s-768, $c8) {
                border-bottom: solid 0.1rem var(--color-main-500);
                text-align: left;
                padding: 1.6rem 0.8rem;
                &:hover {
                    background-color: #000;
                }
                &.active {
                    background-color: #000;
                }
            }
        }
    }
    &__btn-toggle {
        position: absolute;
        width: 3rem;
        height: 2rem;
        right: 1.6rem;
        top: 50%;
        transform: translate3d(0,-50%,0);
        display: none;
        border: 0;
        background-color: transparent;
        padding: 0;
        span {
            font-size: 0;
            width: 100%;
            height: 0.2rem;
            background-color: #696767;
            border-radius: 0.4rem;
            position: absolute;
            left: 0;
            top: 0;
            transition: all 300ms ease;
            &:nth-child(1) {
                transform: translate3d(0,0,0);
            }
            &:nth-child(2) {
                transform: translate3d(0,0.9rem,0);
            }
            &:nth-child(3) {
                transform: translate3d(0,1.8rem,0);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            display: block;
        }
        @include susy-breakpoint($s-768, $c8) {
            display: block;
        }
        &.active {
            span {
                &:nth-child(1) {
                    transform: translate3d(0,0.9rem,0) rotate(45deg);
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:nth-child(3) {
                    transform: translate3d(0,0.9rem,0) rotate(-45deg);
                }
            }
        }
    }
}