html {
    font-family: "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", sans-serif;
    height: 100%;
    &.fixed {
        overflow: hidden;
    }
}

body {
    background-color: #fff;
    height: 100%;
    &.hidden {
        overflow: hidden;
    }
}

.site-main {
    position: relative;
}

.wrap {
    position: relative;
}

.btn-gotop {
    background: transparent url('../images/btn-top@3x.png') no-repeat 0 0;
    background-size: cover;
    width: 4.8rem;
    height: 4.8rem;
    position: fixed;
    right: 0;
    bottom: 0;
    z-index: 100;
    padding: 0;
    border: 0;
    transition: all 300ms ease-in-out;
    transform: translate3d(-50%,100%,0);
    &.is-active {
        transform: translate3d(-50%,-50%,0);
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 3.2rem;
        height: 3.2rem;
    }
}